@import url("https://fonts.googleapis.com/css2?family=Josefin+Sans:ital,wght@0,300;0,400;0,500;1,400&family=Ubuntu:wght@300;400;500;700&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  font-family: "Josefin Sans", sans-serif;
}

button {
  transition: transform 0.3s;
}

::-webkit-scrollbar {
  width: 5px;
}

::-webkit-scrollbar-track {
  background: white;
}

::-webkit-scrollbar-thumb {
  background-color: rgba(35, 1, 187, 0.3725490196);
  border-radius: 0.3rem;
}

::-moz-selection {
  background-color: transparent;
}

::selection {
  background-color: transparent;
}

.todo {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  background-color: #fafafa;
  font-weight: 500;
}
.todo__bg {
  width: 100%;
  background-image: linear-gradient(45deg, rgba(238, 145, 250, 0.568627451), rgba(35, 1, 187, 0.6470588235)), url("../../img/bg.jpg");
  background-position: 50% 30%;
  background-repeat: no-repeat;
  background-size: cover;
  position: absolute;
  height: 30rem;
  top: 0;
  transform: translate3d(0, 0, 0);
  -webkit-transform: translate3d(0, 0, 0);
}
.todosec {
  position: absolute;
  width: 40rem;
  height: 80vh;
  display: flex;
  flex-direction: column;
}
.todosec button > svg {
  color: rgba(35, 1, 187, 0.6470588235);
}
.todosec__counter {
  font-size: 1.3rem;
  font-weight: 600;
  color: rgba(0, 0, 0, 0.3);
  width: 8rem;
  height: 5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: default;
}
.todosec__foot {
  width: 100%;
  height: 5.2rem;
  top: 100%;
  left: 0;
  background-color: white;
  position: absolute;
  transform: translateY(-100%);
  border-top: 0.1rem solid rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
  align-items: center;
  padding: 0 2rem;
}
.todosec__clear {
  width: 8rem;
  height: 5rem;
  font-size: 1.3rem;
  background: transparent;
  color: rgba(35, 1, 187, 0.6470588235);
  font-family: inherit;
  font-weight: 600;
  border: none;
  transition: all 0.3s;
  text-shadow: 0.3rem 0.3rem 0.5rem rgba(0, 0, 0, 0.2);
  cursor: pointer;
}
.todosec__clear:hover {
  font-size: 1.45rem;
  transform-origin: center;
}
.todosec__clear:active {
  font-size: 1.5rem;
}
.todosec__p {
  flex: 0 0 10%;
  width: 100%;
  height: 5rem;
  font-size: 3.5rem;
  font-weight: 700;
  font-family: "Ubuntu", sans-serif;
  letter-spacing: 1rem;
  text-align: left;
  color: white;
  text-transform: uppercase;
  text-shadow: 0.2rem 0.2rem 1rem rgba(0, 0, 0, 0.3);
}
.todosec__add {
  width: 100%;
  position: relative;
  margin: 2rem 0 3rem 0;
}
.todosec__add input {
  width: 100%;
  position: relative;
  font-size: 1.9rem;
  padding: 1rem 2rem;
  line-height: 2.2;
  border: none;
  border-radius: 0.5rem;
  box-shadow: 0.2rem 0.2rem 1rem rgba(0, 0, 0, 0.3);
  font-family: inherit;
  font-weight: 500;
  color: rgba(0, 0, 0, 0.6);
}
.todosec__add input::-moz-selection {
  background-color: rgba(0, 0, 0, 0.2);
}
.todosec__add input::selection {
  background-color: rgba(0, 0, 0, 0.2);
}
.todosec__add input:focus, .todosec__add input:active {
  outline: none;
}
.todosec__add button {
  top: 50%;
  right: 0;
  transform: translate(-50%, -50%);
  position: absolute;
  border: none;
  font-size: 2rem;
  width: 4rem;
  height: 4rem;
  background-color: transparent;
  border-radius: 0 0.5rem 0.5rem 0;
}
.todosec__add button:hover {
  transform: translate(-50%, -50%) scale(1.1);
}
.todosec__add button svg {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 80%;
  cursor: pointer;
}
.todosec__box {
  overflow: hidden;
  background-color: white;
  width: 100%;
  flex: 0 0 70%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  border: none;
  border-radius: 0.5rem;
  height: 100%;
  overflow-y: auto;
  overflow-x: hidden;
  position: relative;
}
.todosec__box__out {
  width: 100%;
  flex: 0 0 60%;
  overflow-y: auto;
  overflow-x: hidden;
  box-shadow: 0.8rem 0.8rem 2rem rgba(0, 0, 0, 0.15);
  position: relative;
  border-radius: 0.5rem;
}
.todosec__box li {
  list-style: none;
  text-align: left;
  font-size: 1.8rem;
  padding: 2rem 2rem;
  display: flex;
  align-items: center;
  position: relative;
  text-shadow: 0.3rem 0.3rem 0.8rem rgba(0, 0, 0, 0.03);
  color: rgba(0, 0, 0, 0.6);
  cursor: pointer;
}
.todosec__box li:hover {
  background-color: rgb(250, 250, 250);
}
.todosec__box li {
  border-bottom: 0.1rem solid rgba(0, 0, 0, 0.1);
}
.todosec__box li span {
  position: absolute;
  left: 3rem;
}
.todosec__box li input {
  height: 2rem;
  width: 2rem;
  font-size: 2rem;
  position: relative;
  z-index: 5;
  opacity: 0;
  margin-right: 1.5rem;
}
.todosec__box li input:checked + span::after {
  content: "";
  position: absolute;
  z-index: 9;
  padding: 1px 0;
  width: 0.22rem;
  height: 0.5rem;
  border-right: 0.23rem solid white;
  border-bottom: 0.23rem solid white;
  visibility: visible;
  background-color: transparent;
  opacity: 1;
  transform: rotate(45deg) translate(-130%, -25%);
  transform-origin: center;
  pointer-events: none;
}
.todosec__box li input:checked + span::before {
  border: none;
  width: 2.3rem;
  height: 2.3rem;
  background: linear-gradient(45deg, rgba(238, 145, 250, 0.568627451), rgba(35, 1, 187, 0.6470588235));
}
.todosec__box li span::before {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 2.1rem;
  height: 2.1rem;
  border: 0.1rem solid rgba(0, 0, 0, 0.2);
  border-radius: 50px;
  visibility: visible;
  cursor: pointer;
}
.todosec__box li button {
  margin: 0 2rem;
  position: absolute;
  right: 0;
  width: 4rem;
  height: 4rem;
  background: transparent;
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
}
.todosec__box li button:hover {
  -webkit-animation: rotate-center 0.6s ease-in-out both;
  animation: rotate-center 0.6s ease-in-out both;
}
.todosec__box li button svg {
  width: 100%;
  height: 75%;
  cursor: pointer;
}

.complete {
  text-decoration: line-through;
  color: rgba(0, 0, 0, 0.3) !important;
}

@media only screen and (max-width: 960px) {
  html {
    font-size: 60%;
  }
  .todosec {
    width: 42%;
  }
}
@media only screen and (max-width: 780px) {
  html {
    font-size: 58%;
  }
  .todosec {
    width: 50%;
  }
}
@media only screen and (max-width: 570px) {
  html {
    font-size: 55%;
  }
  .todosec {
    width: 55%;
  }
}
@media only screen and (max-width: 480px) {
  html {
    font-size: 53%;
  }
  .todosec {
    width: 60%;
  }
}
@media only screen and (max-width: 380px) {
  html {
    font-size: 48%;
  }
  .todosec {
    width: 75%;
  }
}
@media only screen and (max-width: 298px) {
  html {
    font-size: 45%;
  }
  .todosec {
    width: 90%;
  }
}
@-webkit-keyframes rotate-center {
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(360deg);
  }
}
@keyframes rotate-center {
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(360deg);
  }
}